._lock {
  overflow: hidden;

  body {
    position: relative;
  }

  body::after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: $mainColor, $alpha: 0);
    backdrop-filter: blur(3px);
    z-index: 100;
    cursor: pointer;
  }
}

._btn {
  background-color: rgba($color: $accent, $alpha: 1.0);
  font-weight: 600;
  height: 40px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba($color: $white, $alpha: 1.0);
  transition: $animation_speed;

  @media (hover:hover) {
    &:hover {
      background-color: rgba($color: $hoverBtn, $alpha: 1);
    }
  }
}

._title {}

.empty-info {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 400px;

  p {
    font-size: 40px;
    font-weight: 600;
    color: rgba($color: $accent, $alpha: .5);
    width: 100%;
    text-align: center;
  }
}

section {
  padding: 80px 0px;
}

h1 {}

h2 {}

h3 {}

h4 {}

h5 {}

._title {
  font-size: 45px;
  text-wrap: balance;
  line-height: 120%;
  text-transform: uppercase;
  margin-bottom: 20px;
  color: rgba($color: $mainColor, $alpha: 1.0);

  span {
    font-size: inherit;
    line-height: inherit;
    font-weight: 600;
  }
}

._title-h2 {
  font-size: 40px;
  text-wrap: balance;
  line-height: 120%;
  text-transform: uppercase;
  margin-bottom: 20px;
  color: rgba($color: $mainColor, $alpha: 1.0);

  span {
    font-size: inherit;
    line-height: inherit;
    font-weight: 600;
  }
}